import { graphql, PageProps } from "gatsby"
import { FileSystemNode } from "gatsby-source-filesystem"
import mailtoLink from "mailto-link"
import React, {
  createContext,
  FC,
  FunctionComponent,
  PropsWithChildren,
  useContext,
  useEffect,
  useState,
} from "react"
import { event, OutboundLink } from "react-ga"
import { BsArrowCounterclockwise, BsEnvelopeFill } from "react-icons/bs"
import { FaPhone } from "react-icons/fa"
import {
  Button,
  Card,
  CardText,
  CardTitle,
  Col,
  Container,
  FormGroup,
  Input,
  Label,
  Modal,
  ModalBody,
  ModalFooter,
  ModalHeader,
  Row,
} from "reactstrap"
import { ChildMarkdownRemark, ChildPlainText } from "src/util"
import format from "string-format"
import { CollapsibleSection } from "../components/collapsible-section"
import { PageHeader, StandardLayout } from "../components/layout"
import {
  createContactInfoQuestion,
  createDisplayQuestion,
  MultiPageForm,
} from "../components/multi-page-form"
import SEO from "../components/seo"

export const pageQuery = graphql`
  fragment ActionContactGroup on toolkitcontactinfoYaml {
    email
    form
    title
    members {
      email
      name
      phone
      extra
    }
    phone
  }

  query TakeActionQuery {
    email: file(relativePath: { eq: "action-toolkit/email.template.txt" }) {
      childPlainText {
        content
      }
    }
    phone: file(relativePath: { eq: "action-toolkit/phone.template.txt" }) {
      childPlainText {
        content
      }
    }
    defaults: toolkitformatdefaultsYaml {
      da {
        demandEmail
        demandPhone2
        demandPhone1
        needToListen
        to
      }
      shared {
        demandEmail
        to
        supportDemands
        shownRacism
        personalReason
        needToListen
        name
        location
        demandPhone2
        demandPhone1
      }
    }
    slo: toolkitcontactinfoYaml(key: { eq: "slo" }) {
      ...ActionContactGroup
    }
    da: toolkitcontactinfoYaml(key: { eq: "da" }) {
      ...ActionContactGroup
    }
    county: toolkitcontactinfoYaml(key: { eq: "county" }) {
      ...ActionContactGroup
    }
    article: file(relativePath: { eq: "action-toolkit/other.md" }) {
      childMarkdownRemark {
        html
      }
    }
    demands: file(relativePath: { eq: "policy/demands/simple.md" }) {
      childMarkdownRemark {
        html
      }
    }
  }
`

type ContactInfo = {
  name: string
  email: string
  phone: string
  extra?: string
}

type ContactGroup = {
  email?: string
  title?: string
  form?: string
  phone: string
  members: ContactInfo[]
}

type Data = {
  email: FileSystemNode & ChildPlainText
  phone: FileSystemNode & ChildPlainText
  da: ContactGroup
  slo: ContactGroup
  county: ContactGroup
  defaults: any
  article: ChildMarkdownRemark
  demands: ChildMarkdownRemark
}

type EmailGeneratorProps = {}
const EmailGenerator: FC<EmailGeneratorProps> = () => {
  const [message, setMessage] = useState(null as string | null)
  return (
    <section>
      <MultiPageForm
        style={{ minHeight: 270 }}
        questions={
          [
            createDisplayQuestion(
              <p>
                This is the email template generator form thing stuff we can
                change this bit here later
              </p>
            ),
            createContactInfoQuestion(),
          ] as const
        }
        onComplete={([_, { first, last, email }]) => {
          setMessage(
            `This is a message specially crafted for ${first} ${last} <${email}>`
          )
          return
        }}
      >
        <p>
          Here is your email, you can copy it. Or, click on any of the email
          addresses below to automatically create a email with this message.
        </p>
      </MultiPageForm>
      {message == null ? null : <p>{message}</p>}
    </section>
  )
}

type ContactScript = {
  to: string
  body: string
  setBody: (body: string) => void
  resetEmail: () => void
  phone: string
}

const ContactScriptKeyContext = createContext(
  (null as unknown) as { key: (ctx: FormattedTemplateData) => ContactScript }
)

type FormattedTemplateData = {
  setUserFormat: (any: string) => void
  shared: ContactScript
  da: ContactScript
}

const FormattedTemplateContext = createContext(
  (null as unknown) as FormattedTemplateData
)

const FormattedTemplateProvider: FC<PropsWithChildren<{
  emailTemplate: string
  phoneTemplate: string
  defaultFormat: any
}>> = ({ emailTemplate, phoneTemplate, defaultFormat, children }) => {
  const [sharedBody, setSharedBody] = useState("")
  const [daBody, setDABody] = useState("")
  const [userFormat, setUserFormat] = useState({} as any)

  const sharedParams = {
    ...defaultFormat.shared,
    ...userFormat,
  }
  const daParams = {
    ...defaultFormat.shared,
    ...defaultFormat.da,
    ...userFormat,
    to: "District Attorney Dan Dow",
  }
  const resetShared = () => {
    setSharedBody(format(emailTemplate, sharedParams))
  }
  const resetDA = () => {
    setDABody(format(emailTemplate, daParams))
  }
  useEffect(() => {
    resetShared()
    resetDA()
  }, [])

  return (
    <FormattedTemplateContext.Provider
      value={{
        shared: {
          to: sharedParams.to,
          body: sharedBody,
          setBody: setSharedBody,
          resetEmail: resetShared,
          phone: format(phoneTemplate, sharedParams),
        },
        da: {
          to: daParams.to,
          body: daBody,
          setBody: setDABody,
          resetEmail: resetDA,
          phone: format(phoneTemplate, daParams),
        },
        setUserFormat: userFormat => {
          setUserFormat(userFormat)
          setSharedBody(format(emailTemplate, sharedParams))
        },
      }}
    >
      {children}
    </FormattedTemplateContext.Provider>
  )
}

const PrefilledEmailLink: FC<{
  to: string
  address: string
}> = ({ to, address }) => {
  const context = useContext(FormattedTemplateContext)
  const { key } = useContext(ContactScriptKeyContext)
  const uri = mailtoLink({
    to: address,
    body: `To ${to}:\n\n` + key(context).body,
  }) as string
  return (
    <a
      href={uri}
      onClick={() => {
        event({
          category: "Action Toolkit",
          action: `Used mailto link`,
          label: to,
        })
      }}
    >
      {address}
    </a>
  )
}

const PhoneLink: FC<{ phone: string; to: string }> = ({ phone, to }) => {
  const phoneURI = `tel:+1${phone.replace(/[^0-9]/g, "")}`
  return (
    <a
      href={phoneURI}
      onClick={() => {
        event({
          category: "Action Toolkit",
          action: `Used tel link`,
          label: to,
        })
      }}
    >
      {phone}
    </a>
  )
}

const PhoneNumberIcon = () => <FaPhone title="Phone number" />
const EmailIcon = () => <BsEnvelopeFill title="Email address" />

const ContactTarget: FC<{ info: ContactInfo; title: string }> = ({
  info: { name, email, phone, extra },
  title,
}) => {
  const [_, last] = name.split(" ")
  const to = title + " " + last
  return (
    <Card body>
      <CardTitle>
        <h6>
          {name}
          {extra ? <> ({extra})</> : null}
        </h6>
      </CardTitle>
      <CardText>
        <EmailIcon /> <PrefilledEmailLink address={email} to={to} />
      </CardText>
      <CardText>
        <PhoneNumberIcon /> <PhoneLink phone={phone} to={to} />
      </CardText>
    </Card>
  )
}

const ContactTargetDeck: FC<{ title: string; infos: ContactInfo[] }> = ({
  title,
  infos,
}) => (
  <Row style={{ marginTop: 10 }}>
    {infos.map(m => (
      <Col style={{ marginBottom: 20 }} xs="12" sm="12" lg="4" xl="3">
        <ContactTarget title={title} info={m} />
      </Col>
    ))}
  </Row>
)

const SamplePhoneScript: FunctionComponent = () => {
  const context = useContext(FormattedTemplateContext)
  const { key } = useContext(ContactScriptKeyContext)

  return (
    <article>
      <h4>Phone script</h4>
      <p>{key(context).phone}</p>
    </article>
  )
}

const EmailField: FC = () => {
  const context = useContext(FormattedTemplateContext)
  const { key } = useContext(ContactScriptKeyContext)
  const contact = key(context)

  const [modalOpen, setModalOpen] = useState(false)

  return (
    <div>
      <h4>Email template</h4>
      <p className="text-muted">
        Editing this field will copy your message to the email links.
      </p>
      <FormGroup>
        <Label for="emailDuplicator">To {contact.to}:</Label>
        <Input
          type="textarea"
          name="text"
          id="emailDuplicator"
          style={{ minHeight: 350 }}
          value={contact.body}
          onChange={ev => {
            contact.setBody(ev.target.value)
            event({
              category: "Action Toolkit",
              action: `Changed email body`,
              label: contact.to,
            })
          }}
        />
      </FormGroup>
      <p className="text-right">
        <Button color="danger" onClick={() => setModalOpen(true)}>
          <BsArrowCounterclockwise title="Reset" />
        </Button>
      </p>
      <Modal isOpen={modalOpen} toggle={() => setModalOpen(!modalOpen)}>
        <ModalHeader>Heads up!</ModalHeader>
        <ModalBody>Are you sure you want to reset all your changes?</ModalBody>
        <ModalFooter>
          <Button color="secondary" onclick={() => setModalOpen(false)}>
            No, go back!
          </Button>
          <Button
            color="danger"
            onClick={() => {
              contact.resetEmail()
              setModalOpen(false)
              event({
                category: "Action Toolkit",
                action: "Reset email body",
                label: contact.to,
              })
            }}
          >
            Yes, reset my changes!
          </Button>
        </ModalFooter>
      </Modal>
    </div>
  )
}

const ActionToolkit1: FC<PageProps<Data>> = ({
  data: { slo, county, da, defaults, article, email, demands, phone },
}) => {
  return (
    <StandardLayout>
      <SEO title="Take Action!" />
      <Container tag="main">
        <PageHeader pageTitle="Take Action Now!" />
        <ol className="h4">
          <li>
            Fill out the embedded form to sign your name and commit to actions.
          </li>
          <li>
            Follow through on those actions! See below for call/email templates,
            email addresses, and tools!
          </li>
        </ol>

        <hr />

        <section>
          <h2>Sign on!</h2>
          <article
            dangerouslySetInnerHTML={{
              __html: article.childMarkdownRemark.html,
            }}
          />
          <iframe
            style={{ width: "100%", height: "50vh", maxHeight: 500 }}
            onFocus={() => {
              event({
                category: "Action Toolkit",
                action: "Focus on sign-on form",
              })
            }}
            src="https://docs.google.com/forms/d/e/1FAIpQLSfHxthBPdQFN95B8g9VRUHgLX7aZ_3rDHoIctuB2rV5s2gEdA/viewform?usp=sf_link"
            title="Sign-on form"
          />
        </section>

        <hr />

        <FormattedTemplateProvider
          emailTemplate={email.childPlainText.content}
          phoneTemplate={phone.childPlainText.content}
          defaultFormat={defaults}
        >
          <section>
            <ContactScriptKeyContext.Provider
              value={{ key: ctx => ctx.shared }}
            >
              <h2>
                Call and Email SLO City Council, the County Supervisors, and
                D.A. Dow
              </h2>
              <p>Please fill in your own words where indicated.</p>
              <p className="h5 text-danger">
                This is especially important for emails, since they're known to
                use filters to mass-delete messages! Remember it is critical to
                change up the wording!
              </p>
              <CollapsibleSection
                title="Click to see Demands"
                html={demands.childMarkdownRemark.html}
              />
              <SamplePhoneScript />
              <EmailField />

              <section>
                <h3>SLO City Council</h3>
                <p>
                  <big>
                    <PhoneNumberIcon />{" "}
                    <PhoneLink to="SLO City Council" phone={slo.phone} />
                  </big>
                </p>
                <p>
                  <EmailIcon />{" "}
                  <PrefilledEmailLink
                    to="SLO City Council"
                    address={slo.email!}
                  />
                </p>
                <ContactTargetDeck title={slo.title!} infos={slo.members} />
                <h3>SLO County Supervisors</h3>
                <p>
                  <big>
                    <PhoneNumberIcon />{" "}
                    <PhoneLink to="SLO County" phone={county.phone} />
                  </big>
                </p>
                <Button
                  color="primary"
                  tag={OutboundLink}
                  to={county.form!}
                  eventLabel="SLO County Form"
                  target="_blank"
                >
                  Send your message on their form
                </Button>
                <ContactTargetDeck
                  title={county.title!}
                  infos={county.members}
                />
              </section>
            </ContactScriptKeyContext.Provider>

            <ContactScriptKeyContext.Provider value={{ key: ctx => ctx.da }}>
              <section>
                <h3>District Attorney Dan Dow</h3>
                <SamplePhoneScript />
                <p>
                  <big>
                    <PhoneNumberIcon />{" "}
                    <PhoneLink to="D.A. Dow" phone={da.phone} />
                  </big>
                </p>
                <EmailField />
                <p>
                  <EmailIcon />{" "}
                  <PrefilledEmailLink
                    to="District Attorney Dan Dow"
                    address={da.email!}
                  />
                </p>
                <Button
                  color="primary"
                  tag={OutboundLink}
                  to={county.form!}
                  eventLabel="D.A. Form"
                  target="_blank"
                >
                  Send your message on his form
                </Button>
              </section>
            </ContactScriptKeyContext.Provider>
          </section>
        </FormattedTemplateProvider>
      </Container>
    </StandardLayout>
  )
}

export default ActionToolkit1
